}

html {
    overflow-y: scroll;
    min-height: 100%;
    margin: 0 0 1px;
    font-size: 100.01%;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    -moz-osx-font-smoothing: grayscale;
    -webkit-overflow-scrolling: touch;
}

body {
    min-height: 100%;
    padding-top: 28px;
    background: #e6e6e6;
    font-size: 62.5%;
